import React, { Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";

import PageTitle from "../../../Layout/AppMain/PageTitle";

import Tabs, { TabPane } from "rc-tabs";
import TabContent from "rc-tabs/lib/SwipeableTabContent";
import ScrollableInkTabBar from "rc-tabs/lib/ScrollableInkTabBar";
import Listing from "./Listing/index";
import Form from "./Form/index";

import {
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
    ListGroup,
  ListGroupItem,
} from "reactstrap";
import {
  setPermissionData
} from '../../../reducers/ThemeOptions';
import {connect} from 'react-redux';

import MyGlobleSetting from '../../MyGlobleSetting';
class CardsExamples extends React.Component {
  render() {
    var permissionData=[];
    if(this.props.permissionData && Object.keys(this.props.permissionData).length >0)
    {  
      permissionData=JSON.parse(this.props.permissionData);
      permissionData=permissionData['role'];
    }
    return (
      <Fragment>
        {this.props.location.pathname == '/jobs' ?
          <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
            transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
            <PageTitle heading="Jobs"
              subheading="Jobs"
              icon="pe-7s-smile icon-gradient bg-amy-crisp"/>
            <Row>
              <Card style={{width:'100%'}}>
                <CardBody>
                  <Listing permission={permissionData} imageUrl={MyGlobleSetting.state.imageUrl} />
                </CardBody>
              </Card>
            </Row>
          </CSSTransitionGroup>
        :
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
            transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
            <PageTitle heading="jobs"
              subheading="jobs"
              icon="pe-7s-smile icon-gradient bg-amy-crisp"/>
            <Row>
              <Card style={{width:'100%'}}>
                <CardBody>
                  <Form permission={permissionData} />
                </CardBody>
              </Card>
            </Row>
          </CSSTransitionGroup>
      }
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  permissionData:state.ThemeOptions.permissionData,
});

const mapDispatchToProps = dispatch => ({
  setPermissionData: enable => dispatch(setPermissionData(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(CardsExamples);
