import React, { Fragment } from "react";
import { Route } from "react-router-dom";
import Job from "./Job/";

// Layout
import AppHeader from "../../Layout/AppHeader/";
import AppSidebar from "../../Layout/AppSidebar/";
import AppFooter from "../../Layout/AppFooter/";
// Theme Options
import ThemeOptions from "../../Layout/ThemeOptions/";
import {
  setLoaderFlag,
  setNotificationFlag,
  setPermissionData
} from '../../reducers/ThemeOptions';
import {connect} from 'react-redux';
import MyGlobleSetting from '../MyGlobleSetting';
class Jobs extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      permission:[],
    };
  }
  async componentDidMount() {
    let { permissionData, setPermissionData } = this.props;
    await setPermissionData(localStorage.getItem('permisions'));
  }
  render() {
    return (
      <Fragment>
        <ThemeOptions />
        <AppHeader />
        <div className="app-main">
          <AppSidebar />
          <div className="app-main__outer">
            <div className="app-main__inner">
              <Route path={`${this.props.match.url}`} imageUrl={MyGlobleSetting.state.imageUrl} permission={this.state.permission['role']} component={Job} />
              
            </div>
            <AppFooter />
          </div>
        </div>
      </Fragment>
    );
  }
}
const mapStateToProps = state => ({
  permissionData:state.ThemeOptions.permissionData,
});

const mapDispatchToProps = dispatch => ({
  setPermissionData: enable => dispatch(setPermissionData(enable)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Jobs);

