import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import Tabs from "react-responsive-tabs";
import { Table } from 'reactstrap';
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  ListGroup,
  ListGroupItem,
} from "reactstrap";

import { toast, Bounce } from "react-toastify";
import CountUp from "react-countup";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  faAngleUp,
  faAngleDown,
  faDotCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import ApiCall from "../../../Services/Function/ApiCall";
import Loading from '../../../Loading';
import MyGlobleSetting from '../../../MyGlobleSetting';
class index extends Component {
   constructor(props) {
    super(props);
    this.state = {
      name:null,
      id:0,
      flag:false,
      active:true,
      serviceTypeId:null,
      is_response:false,
      serviceType:[],
      dbUrl:"/Clients/Temp16-Mar-2023 01.58.11.645 AM~mem2.png",
      localUrl:"D:/InetPub/vhosts/happijobs.com/inxgo.com/wwwroot/Clients/Temp/16-Mar-2023 01.58.11.645 AM~mem2.png",
    };
  }
  HandleSetState=(key,value)=>
  {
    this.setState({
      [key]: value
    });
  }
  handleAddSkills=(event)=>
  {
    this.HandleSetState('name',event.target.value);
  }
  handleSubmit=async(e)=>
  {
    this.HandleSetState('is_response',true);
    e.preventDefault();
    let data={
      'id':this.state.id,
      'title':this.state.name,
      'serviceTypeId':parseInt(this.state.serviceTypeId),
      "active": this.state.active,
      "photo": {
        "localUrl": this.state.localUrl,
        "dbUrl": this.state.dbUrl
      }
    }
    var url='Skill/add';
    if(this.state.id != 0)
    {
      url='Skill/update';
    }
    let response=await ApiCall.postAPICall(2,url,data);
    var type='success';
    var html='';
    if(response.code == 200)
    {
      html=response.message;
    }
    else
    {
      type='error';
      if(typeof(response.message) !='undefined')
      {
        html=response.message;
      }
      else
      {
        html='Something went wrong..!';
      }
    }
    (this.toastId = toast(
      html,
      {
        transition: Bounce,
        closeButton: true,
        autoClose: 2000,
        position: "bottom-center",
        type: type,
        bodyOutputType: 'trustedHtml'
      }

    ))
    this.HandleSetState('name',null);
    this.HandleSetState('id',null);
    this.HandleSetState('serviceTypeId',null);
    this.HandleSetState('is_response',false);
  }
  async componentDidMount() {
    this.HandleSetState('flag',true);
    let response=await ApiCall.getAPICall(2,'ServiceType/Get?GetAll=true&IsActive=true&IsSeller=true');
    this.HandleSetState('serviceType',response.data);
    if(localStorage.getItem('skills_edit'))
    {
      let edit_data=JSON.parse(localStorage.getItem('skills_edit'));
      this.HandleSetState('id',edit_data.id);
      this.HandleSetState('name',edit_data.title);
      this.HandleSetState('serviceTypeId',edit_data.serviceTypeId);
      let localUrl=MyGlobleSetting.state.serverImageUrl+edit_data.photoPath;
      localUrl=localUrl.replace("Clients/Temp","Clients/Temp/");
      this.HandleSetState('localUrl',localUrl);
      this.HandleSetState('dbUrl',edit_data.photoPath);
    }
  }
  handleSelectOnChange=(id)=>
  {
    this.setState({
        'serviceTypeId': id
      });
  }
  HandleDisplay=()=>
  {
     return (
      <Row className="d-flex justify-content-center">
         <form className="col-md-12 row">
            <div className="col-md-6 form-group">
              <label >Skills</label>
              <input type="text" value={this.state.name} onChange={this.handleAddSkills} className="form-control" placeholder="Enter Skills"/>
              <small className="form-text text-muted"></small>
            </div>
            <div className="col-md-6 form-group">
              <label >Service Type</label>
              <select value={this.state.serviceTypeId} onChange = {(e)=> this.handleSelectOnChange(e.target.value)} className="form-control" name={'name'} >
                <option value="">Select service Type</option>
                {typeof(this.state.serviceType) !='undefined'?
                  Object.keys(this.state.serviceType).map((val, i) => (
                    <option value={this.state.serviceType[i]['id']}>{this.state.serviceType[i]['title']}</option>
                  ))
                  :null
                }
              </select>
            </div>
            <div className=" col-md-12 form-group">
              <label >Skills Image</label>
              <input type="file" value={this.state.file} onChange={this.handleAddSkillsTypeImage} className="form-control" placeholder="Upload Skills Type Image"/>
              <small className="form-text text-muted"></small>
            </div>
            {this.state.is_response?
              <span className="col-md-1">
                <Loading/>
              </span>
              :
              <button type="submit" className="ml-3 btn btn-primary" onClick={(e) => this.handleSubmit(e)}>Submit</button>
            }
          </form>
      </Row>);
  }
  render() {
    return (
      <Fragment>
        {this.state.flag ? 
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
          transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
          <Container fluid>
            {this.HandleDisplay()}
          </Container>
        </CSSTransitionGroup>
        :
          <Loading/>
        }
      </Fragment>
    );
  }
}

export default index;
