import React, { Component, Fragment } from "react";
import CSSTransitionGroup from "react-transition-group/CSSTransitionGroup";
import classnames from "classnames";
import Tabs from "react-responsive-tabs";
import { Table } from 'reactstrap';
import ReactTable from "react-table";
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  CardFooter,
  Button,
  ButtonGroup,
  UncontrolledButtonDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  ListGroup,
  ListGroupItem,
} from "reactstrap";
import CountUp from "react-countup";
import avatar1 from "../../../../assets/utils/images/avatars/2.jpg";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  faAngleUp,
  faAngleDown,
  faDotCircle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from 'react-router-dom';
import ApiCall from "../../../Services/Function/ApiCall";
import Loading from '../../../Loading';
import { toast, Bounce } from "react-toastify";
// import fileExists  from 'file-exists';
class index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data:{},
      flag:false,
    };
  }
  handleAddButton=()=>{
    localStorage.setItem('skills_edit',[]);
    let url =window.location.href+'/add';
    window.location.href=url;
  }
  HandleSetState=(key,value)=>
  {
    this.setState({
      [key]: value
    });
  }
  getdata=async()=>
  {
    let response=await ApiCall.getAPICall(2,'Job/get?GetAll=true&IsActive=true&IsSeller=true');
    console.log(response.data);
    this.HandleSetState('data',response.data); 
  }
  async componentDidMount() {
    await this.getdata();
    this.HandleSetState('flag',true);
  }
  handleDeleteButton=async(id)=>
  {
    let url='skills/delete';
    let obj={
      'id':id
    }
    let response=await ApiCall.postAPICall(1,url,obj);
    var type='success';
    var html='';
    if(response.code == 401)
    {
      type='error';
      Object.keys(response.error).map((val, i) => {
        html=html+response.error[val][0]+'</br>';
      })
    }else
    {
      html=response.message;
      this.getdata();
    }
    (this.toastId = toast(
      html,
      {
        transition: Bounce,
        closeButton: true,
        autoClose: 2000,
        position: "bottom-center",
        type: type,
      }
    ))
    
  }
  handleEditButton=async(data)=>
  {
    localStorage.setItem('skills_edit', JSON.stringify(data));
    let url =window.location.href+'/add';
    window.location.href=url;
  }
  imgError=()=>
  {
    console.log('The image could not be loaded.');
  }
  HandleDisplay=()=>
  {
    const permission = this.props.permission;
    var imageUrl=this.props.imageUrl;
    return (
      <Row className="d-flex justify-content-center">
        <div className="col-md-12">
         {this.props.permission['add']?
          <span className="pull-right" onClick={this.handleAddButton}>
            <Button  className="mb-2 mr-2" color="success">
            Add
            </Button>
          </span>
          :null}
        </div>
         {this.props.permission['view']?
        <CardBody>
          <ReactTable data={this.state.data}
            columns={[
              {
                columns: [
                  {
                    Header: "Sr #",
                    accessor: "id",
                    headerStyle: {textAlign: 'left'},
                    Cell: (row) => {
                        return <div>{row.index+1}</div>;
                    }
                  }
                ],
              },
              {
                columns: [
                  {
                    Header: "Skills",
                    accessor: "title",
                    headerStyle: {textAlign: 'left'}
                  }
                ],
              },
              {
                columns: [
                  {
                    Header: "Photo",
                    accessor: "photoPath",
                    headerStyle: {textAlign: 'left'},
                    Cell: (row) => {
                      var ret = row.value.replace('/Clients/Temp','');
                      ret=imageUrl+ret;
                      // console.log(fileExists.sync(ret))

                      // console.log(avatar1);
                        return   <img src={ret} height={50} width={50} onError="this.imgError" alt="Skills"/>;
                    }
                  }
                ],
              },
              {
                columns: [
                  {
                    Header: "Action",
                    accessor: "action",
                    headerStyle: {textAlign: 'left'},
                    Cell: (row) => (
                        <span>
                          {permission['edit']?
                            <Button onClick={()=>this.handleEditButton(row.original)} className="mb-2 mr-2" color="primary">
                              Edit
                            </Button>
                            :null}
                            {permission['delete']?
                            <Button onClick={()=>this.handleDeleteButton(row.original.id)} className="mb-2 mr-2" color="danger">
                              Delete
                            </Button>
                            :null}
                        </span>
                              
                            ),
                  },
                ],
              },
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          />
        </CardBody>
        :'Not Access'}
      </Row>);
  }
  render() {
    return (
      <Fragment>
        {this.state.flag ? 
        <CSSTransitionGroup component="div" transitionName="TabsAnimation" transitionAppear={true}
          transitionAppearTimeout={0} transitionEnter={false} transitionLeave={false}>
          <Container fluid>
            {this.HandleDisplay()}
          </Container>
        </CSSTransitionGroup>
        :
          <Loading/>
        }
      </Fragment>
    );
  }
}

export default index;
